.profile {
    max-width: 100%;
    box-sizing: border-box;
    padding-top: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (max-width: 940px) {
    .profile {
        flex-direction: column;
        padding-top: 42px;
    }
}